adow: 2px 4px 6px 0 rgba(0, 0, 0, 0.4);\n}\n:host.pointed .arrow {\n  position: absolute;\n  z-index: 1;\n  left: 50%;\n  transform: translateX(-50%);\n  width: 0;\n  padding-left: 16px;\n  padding-bottom: 8px;\n  background-clip: content-box;\n  overflow: hidden;\n}\n:host.pointed .arrow:before {\n  content: \"\";\n  position: absolute;\n  left: 0;\n  top: 0;\n  width: 10px;\n  padding-bottom: 10px;\n  background-color: inherit;\n  border: 1px solid;\n  transform: rotate(45deg);\n  left: 2px;\n  top: 3px;\n  background-color: #FFFFFF;\n  border: 1px solid #CBCFD1;\n}\n:host.pointed .panel {\n  box-shadow: 2px 4px 6px 0 rgba(0, 0, 0, 0.1);\n  border: 1px solid #CBCFD1;\n}\n:host.center button[text]:before {\n  padding-right: 10px;\n}\n:host.center .panel {\n  left: 0;\n}\n:host.left button[text] {\n  flex-direction: row-reverse;\n}\n:host.left button[text]:before {\n  padding-right: 10px;\n}\n:host.left .panel {\n  left: 0;\n}\n:host.right button[text]:before {\n  padding-right: 10px;\n}\n:host.right .panel {\n  right: 0;\n}\n:host.top .arrow {\n  bottom: calc(100% - 7px);\n  transform: translateX(-50%) rotate(180deg);\n}\n:host.top .panel {\n  bottom: 100%;\n}\n:host.bottom .arrow {\n  top: calc(100% - 7px);\n}\n:host.bottom .panel {\n  top: 100%;\n}\n.panel-class {\n  max-height: 300px;\n  overflow-y: auto;\n}\n.panel-class::-webkit-scrollbar {\n  width: 8px;\n}\n.panel-class::-webkit-scrollbar-thumb {\n  border-radius: 4px;\n  background-color: #B5B5B5;\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"